"use client";
import React, { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import Image from "next/image";
import { fetchClassroomById, fetchStudents, fetchClassroomStudents, addStudentsToClassroom, fetchReportCards, createReportCard } from "@/lib/api";
import { Button } from "@/components/ui/button";
import StudentTable from "@/components/dashboard/studentTable";
import CreateReportCardModal from "@/components/dashboard/createReportCard";

type Student = {
    id: string;
    name: string;
    avatar?: string;
    gender?: string;
    parentPhone?: string;
    dob?: string;
    address?: string;
    userId?: string;
    createdAt?: string;
    // add other fields as needed
};

const ClassroomName = ({ id }: { id: string }) => {
    const [classroom, setClassroom] = useState<{
        id: string;
        name: string;
    } | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setLoading(true);
        fetchClassroomById(id)
            .then((data) => {
                setClassroom(data);
                setLoading(false);
            })
            .catch(() => {
                setClassroom(null);
                setLoading(false);
            });
    }, [id]);

    if (loading) return <span className="text-gray-400">Loading...</span>;
    if (!classroom)
        return <span className="text-red-500">Classroom Not Found</span>;
    return <span className="text-xl font-semibold">{classroom.name}</span>;
};

const AddStudentModal = ({
    open,
    onClose,
    onAdd,
    students,
    selected,
    setSelected,
}: {
    open: boolean;
    onClose: () => void;
    onAdd: () => void;
    students: Student[];
    selected: string[];
    setSelected: (ids: string[]) => void;
}) => {
    // Add Escape key handler
    useEffect(() => {
        if (!open) return;
        const handleKeyDown = (e: KeyboardEvent) => {
            if (e.key === "Escape") {
                onClose();
            }
        };
        document.addEventListener("keydown", handleKeyDown);
        return () => {
            document.removeEventListener("keydown", handleKeyDown);
        };
    }, [open, onClose]);

    if (!open) return null;
    return (
        <div
            className="fixed inset-0 bg-black/30 flex items-center justify-center z-50"
            onClick={onClose}
            tabIndex={-1}
            aria-modal="true"
            role="dialog"
        >
            <div
            className="bg-white rounded-xl p-6 w-full max-w-md"
            onClick={(e) => e.stopPropagation()}
            tabIndex={0}
            >
            <div className="text-center font-bold text-lg mb-4">បញ្ចូលសិស្ស</div>
            <div className="flex flex-wrap gap-2 mb-4">
                {students
                .filter((s) => selected.includes(s.id))
                .map((s) => (
                    <span key={s.id} className="flex items-center px-2 py-1 rounded-full bg-gray-100 border">
                    {s.avatar && (
                        <Image
                        src={s.avatar}
                        alt={s.name}
                        width={24}
                        height={24}
                        className="w-6 h-6 rounded-full mr-1"
                        />
                    )}
                    {s.name}
                    </span>
                ))}
            </div>
            <hr className="mb-4" />
            <div className="space-y-3 mb-6 max-h-60 overflow-y-auto">
                {students.map((s) => (
                <div
                    key={s.id}
                    className="flex items-center cursor-pointer"
                    onClick={() =>
                    setSelected(
                        selected.includes(s.id)
                        ? selected.filter((id) => id !== s.id)
                        : [...selected, s.id]
                    )
                    }
                >
                    <span className={`w-6 h-6 mr-2 flex items-center justify-center rounded-full border-2 ${selected.includes(s.id) ? "border-blue-600 bg-blue-50" : "border-gray-300"}`}>
                    {selected.includes(s.id) && (
                        <svg width="18" height="18" fill="none" viewBox="0 0 18 18">
                        <circle cx="9" cy="9" r="8" stroke="#2563eb" strokeWidth="2" />
                        <path d="M6 9l2 2 4-4" stroke="#2563eb" strokeWidth="2" strokeLinecap="round" />
                        </svg>
                    )}
                    </span>
                    {s.avatar && (
                    <Image
                        src={s.avatar}
                        alt={s.name}
                        width={32}
                        height={32}
                        className="w-8 h-8 rounded-full mr-2"
                    />
                    )}
                    <span>{s.name}</span>
                </div>
                ))}
            </div>
            <Button
                className="w-full bg-[#25388C] hover:bg-[#1e2e6d] text-white font-medium text-base py-2 rounded-lg"
                onClick={onAdd}
            >
                បញ្ចូល
            </Button>
            <Button
                className="w-full mt-2 bg-gray-400 hover:bg-gray-600 text-white"
                variant="secondary"
                onClick={onClose}
            >
                បោះបង់
            </Button>
            </div>
        </div>
    );
};

type ReportCard = {
  id: number;
  title: string;
  createdAt: string;
};

const Page = () => {
  const [tab, setTab] = useState(0);
  const params = useParams();
  const [showAddStudentModal, setShowAddStudentModal] = useState(false);

  // Report card modal state
  const [showCreateReportCardModal, setShowCreateReportCardModal] = useState(false);
  const [reportCards, setReportCards] = useState<ReportCard[]>([]);
  const [creating, setCreating] = useState(false);

  // Student states (existing)
  const [allStudents, setAllStudents] = useState<Student[]>([]);
  const [selectedStudents, setSelectedStudents] = useState<string[]>([]);
  const [classroomStudents, setClassroomStudents] = useState<Student[]>([]);

  // Fetch all students (for modal)
  useEffect(() => {
    if (showAddStudentModal) {
      fetchStudents().then(setAllStudents);
    }
  }, [showAddStudentModal]);

  // Fetch classroom students (for listing)
  useEffect(() => {
    if (params.id) {
      fetchClassroomStudents(params.id).then(setClassroomStudents);
    }
  }, [params.id, showAddStudentModal]);

  // Fetch report cards for this classroom
  useEffect(() => {
    if (tab === 0 && params.id) {
      fetchReportCards(params.id).then(setReportCards);
    }
  }, [tab, params.id, showCreateReportCardModal]);

  const handleAddStudents = async () => {
    await addStudentsToClassroom(params.id, selectedStudents);
    setShowAddStudentModal(false);
    setSelectedStudents([]);
    fetchClassroomStudents(params.id).then(setClassroomStudents);
  };

  // Handler to create a new report card
  const handleCreateReportCard = async (title: string) => {
    setCreating(true);
    try {
      const card = await createReportCard(params.id, title);
      setReportCards((prev) => [card, ...prev]);
    } catch {
      alert("Failed to create report card");
    } finally {
      setCreating(false);
    }
  };

  return (
    <section className="w-full rounded-2xl bg-white p-7">
      <div className="flex items-center justify-between mb-6">
        {typeof params.id === "string" && <ClassroomName id={params.id} />}
      </div>
      <div className="mt-4 pt-3">
        <div className="bg-[#fafbfc] rounded-2xl p-6">
          <div className="flex items-center justify-between border-b border-gray-200 mb-6">
            <div className="flex gap-8">
              <button
                className={`pb-2 font-semibold text-base focus:outline-none ${
                  tab === 0
                    ? "border-b-2 border-black text-black"
                    : "text-gray-400 hover:text-black"
                }`}
                onClick={() => setTab(0)}
              >
                Report Card
              </button>
              <button
                className={`pb-2 text-base focus:outline-none ${
                  tab === 1
                    ? "border-b-2 border-black text-black font-semibold"
                    : "text-gray-400 hover:text-black"
                }`}
                onClick={() => setTab(1)}
              >
                Students
              </button>
            </div>
            <div>
              {tab === 0 && (
                <Button
                  className="bg-[#25388C] hover:bg-[#1e2e6d] text-white font-medium text-sm flex items-center gap-2 px-4 py-2 rounded-lg"
                  onClick={() => setShowCreateReportCardModal(true)}
                  disabled={creating}
                >
                  <svg width="18" height="18" fill="none" viewBox="0 0 18 18">
                    <path d="M9 4v10M4 9h10" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                  </svg>
                  បង្កើតតារាពិន្ទុថ្មី
                </Button>
              )}
              {tab === 1 && (
                <Button
                  className="bg-[#25388C] hover:bg-[#1e2e6d] text-white font-medium text-sm flex items-center gap-2 px-4 py-2 rounded-lg"
                  onClick={() => setShowAddStudentModal(true)}
                >
                  <svg width="18" height="18" fill="none" viewBox="0 0 18 18">
                    <path d="M9 4v10M4 9h10" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                  </svg>
                  បញ្ចូលសិស្សទៅក្នុងថ្នាក់
                </Button>
              )}
            </div>
          </div>
          {/* Content box */}
          <div className="bg-white rounded-xl border border-gray-200 p-5">
            {tab === 0 && (
              <div>
                {reportCards.length === 0 ? (
                  <div className="text-gray-400 text-center py-8">No report cards yet.</div>
                ) : (
                  <table className="w-full">
                    <thead>
                      <tr>
                        <th className="text-left py-2">#</th>
                        <th className="text-left py-2">Title</th>
                        <th className="text-left py-2">Created At</th>
                      </tr>
                    </thead>
                    <tbody>
                      {reportCards.map((report, idx) => (
                        <tr key={report.id} className="border-t">
                          <td className="py-2">{idx + 1}</td>
                          <td className="py-2">{report.title}</td>
                          <td className="py-2">{new Date(report.createdAt).toLocaleString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                )}
              </div>
            )}
            {tab === 1 && (
              <StudentTable students={classroomStudents} setStudents={setClassroomStudents} classroomId={params.id} />
            )}
          </div>
        </div>
      </div>
      {/* Add Student Modal */}
      <AddStudentModal
        open={showAddStudentModal}
        onClose={() => setShowAddStudentModal(false)}
        onAdd={handleAddStudents}
        students={allStudents}
        selected={selectedStudents}
        setSelected={setSelectedStudents}
      />
      {/* Create Report Card Modal */}
      <CreateReportCardModal
        open={showCreateReportCardModal}
        onClose={() => setShowCreateReportCardModal(false)}
        onCreate={handleCreateReportCard}
      />
    </section>
  );
};

export default Page;
